<template>
    <FixtureModule name="Soccer" :tourname="tourname" >
        <MatchFixture :data="getMatchData(0)" />
        <MatchFixture :data="getMatchData(1)" />
        <MatchFixture />
    </FixtureModule>
                                    
</template>
                   

<script>
import FixtureModule from './FixtureModule.vue';
import MatchFixture from './MatchFixture.vue';


export default {
    components: {
        FixtureModule,
        MatchFixture
    },
    data() {
    },
    methods: {
        getMatchData(i){
            if( i< 0){
                i = 0;
            }
            const teams = [
                {team1: "PSG", team2:"Barcelona"},
                {team1: "Atletico Madrid", team2:"Borussia Dortmund"},
            ];
            return teams[i];
        },
    }
}
</script>