<template>
    <div class="fixture-team">
        <div class="fixture-team-container ">
            <div class="team-kit-image " :style="imageStyle()">
                <img :src="teamLogo" class="team-kit-image-svg" />
            </div>
        </div>
        <div class="team-name"> {{ name }}</div>
    </div>
</template>

<script>

import logos from "../../../configs/team-logo"

export default {
    props: {
        name: String,
    },
    data(){
        return {
            teamLogo : this.getTeamLogo(),
        }
    },
    methods: {
        getTeamLogo(){
            let logo = logos[this.name];
            if( !logo ){
                logo = logos["Barcelona"];
            }

            return require(`@/assets/images/team-logo/${logo}`)
        },
        imageStyle: ()=>{
            return {
                height: `28px`,
                width: `30px`
            }
        }

    }
}
</script>