<template>
    <div :class="`menu-match-item ${classes??''}` ">
        <div v-if="iconFromTitle(title)" :class="classIconFromTitle(title)"></div>
        <span>{{ title }}</span>
    </div>
</template>

<script>
export default {
    props: {
        title: String,
        icon: String,
        classes: String,
    },
    methods: {
        classIcon(name) {
            return `icon-${name}`
        },

        iconFromTitle(name){
            if( this.icon === false ){
                return false;
            }

            let alias = name.toLowerCase().trim();
            alias = alias.replace(' ', '-');
            if( alias.length < 1){
                return false;
            }

            return alias;
        },

        classIconFromTitle(name) {
            if( !name ){
                return '';
            }
            return `icon-${this.iconFromTitle(name)}`
        }
    }
}
</script>