<template>
    <div class="featured-fixture-home">
        <div class="scroll-container">
            <div class="has-image">
                <div :class="`match-plus ${sportTheme}`">
                    <div class="match-plus-header clickable ">
                        <div :class="`header-image ${sportType}`"></div>

                        <div class="text-wrapper">
                            <div class="primary-header ">{{ name }}</div>
                            <div class="sub-header">{{ tourname }}</div>
                        </div>
                        <div class="link " role="link" tabindex="0">{{ getBtnTitle() }}</div>
                    </div>

                    <div class="featured-fixture-scroller featured-fixture-mouse-mode featured-fixture-widthstate2 ">
                        <div class="featured-fixture-HScroll ">
                            <div class="featured-fixture-scroll-content">
                                <slot />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    components: {
    },
    props: {
        btnTitle: String,
        name: String,
        tourname: String
    },

    data() {
        return {
            
            sportType: this.getSprotType(),
            sportTheme: this.getSprotTheme(),
        }
    },
    methods: {
        getBtnTitle(){
            if( !this.btnTitle ){
                return "Go To List";
            }
        },
        getSprotType() {
            return this.name.toLowerCase();
        },

        getSprotTheme() {
            switch (this.getSprotType()) {
                case "baseball":
                    return "classification16";
                case "hockey":
                    return "classification17";
                case "soccer":
                default:
                    return "classification1";
            }
        }
    }
}
</script>